<template>
  <div>
    <Row>
      <i-col span="24"
        class="bread-crumb">
        <Breadcrumb>
          <BreadcrumbItem v-for="(item,index) in breadcrumbList"
            :to="item.path"
            :key="index">
            <Icon :type="item.icon"></Icon> {{item.name}}
          </BreadcrumbItem>
        </Breadcrumb>
      </i-col>
    </Row>
    <Divider />
    <Row>
      <i-col span="12"
        offset="6">
        <Form ref="tenantInfoFrom"
          :model="tenantInfoFrom"
          :rules="tenantInfoFromValidate"
          :label-width="100">
          <FormItem label=""
            prop="email">
            <i-input v-model="tenantInfoFrom.email"
              placeholder=""></i-input>
          </FormItem>

          <FormItem label=""
            prop="tenantName">
            <i-input v-model="tenantInfoFrom.tenantName"
              placeholder=""></i-input>
          </FormItem>

          <FormItem label=""
            prop="principal">
            <i-input v-model="tenantInfoFrom.principal"
              placeholder=""></i-input>
          </FormItem>

          <FormItem label=""
            prop="mobile">
            <i-input v-model="tenantInfoFrom.mobile"
              placeholder=""></i-input>
          </FormItem>

          <FormItem label=""
            prop="idCardNumber">
            <i-input v-model="tenantInfoFrom.idCardNumber"
              placeholder=""></i-input>
          </FormItem>

          <FormItem label=""
            prop="corporateBankAccount">
            <i-input v-model="tenantInfoFrom.corporateBankAccount"
              placeholder=""></i-input>
          </FormItem>

          <FormItem label=""
            prop="corporateAccountName">
            <i-input v-model="tenantInfoFrom.corporateAccountName"
              placeholder=""></i-input>
          </FormItem>

          <FormItem label=""
            prop="bankType">
            <Select v-model="tenantInfoFrom.bankType"
              placeholder="">
              <Option v-for="item in bankTypeEunmArr"
                :value="item.value"
                :key="item.value">{{ item.label }}</Option>
            </Select>

          </FormItem>

          <FormItem label="">
            <i-switch v-model="tenantInfoFrom.agreeGuaranteeCash"
              :true-value="this.trueValue"
              :false-value="this.falseValue"
              size="large">
              <span slot="open"></span>
              <span slot="close"></span>
            </i-switch>
          </FormItem>

          <FormItem label=""
            v-show="tenantInfoFrom.agreeGuaranteeCash === this.trueValue"
            prop="guaranteeCash">
            <i-input v-model="tenantInfoFrom.guaranteeCash"
              placeholder=""></i-input>
          </FormItem>

          <FormItem label=""
            prop="cityCode">
            <i-input v-model="tenantInfoFrom.cityCode"
              v-if="false"></i-input>
            <i-input v-model="tenantInfoFrom.cityCode"
              v-if="false"></i-input>
            <Cascader :data="initAreaSelectorData"
              :load-data="loadAreaData"
              @on-change="AreaDataHandleChange"
              placeholder=""></Cascader>
          </FormItem>

          <FormItem label=""
            prop="isSubsidiaryCompany">
            <i-switch v-model="tenantInfoFrom.isSubsidiaryCompany"
              :true-value="this.trueValue"
              :false-value="this.falseValue"
              size="large">
              <span slot="open"></span>
              <span slot="close"></span>
            </i-switch>
          </FormItem>

          <FormItem label=""
            prop="status">
            <Select v-model="tenantInfoFrom.status"
              placeholder="   ">
              <Option v-for="item in tenantInfoStatusEunmArr"
                :value="item.value"
                :disabled="item.disabled"
                :key="item.value">{{ item.label }}</Option>
            </Select>
          </FormItem>

          <FormItem>
            <Button type="primary"
              :disabled="globalButtonLoding"
              @click="handleSubmit('tenantInfoFrom')"></Button>
            <Button @click="handleReset('tenantInfoFrom')"
              style="margin-left: 8px"></Button>
          </FormItem>
        </Form>
      </i-col>
    </Row>
  </div>

</template>


<script>
export default {
  data() {
    return {
      routerPath: this.$route.path,
      trueValue: '1',
      falseValue: '0',
      tenantInfoFrom: {
        email: '',
        tenantName: '',
        principal: '',
        mobile: '',
        idCardNumber: '',
        bankType: '',
        corporateBankAccount: '',
        corporateAccountName: '',
        agreeGuaranteeCash: '0',
        guaranteeCash: '',
        provinceCode: '',
        cityCode: '',
        isSubsidiaryCompany: '0',
        status: ''
      },
      tenantInfoStatusEunmArr: [
        {
          value: '0',
          label: ''
        },
        {
          value: '1',
          label: '',
          disabled: true
        },
        {
          value: '2',
          label: '',
          disabled: true
        },
        {
          value: '3',
          label: '',
          disabled: true
        }
      ],
      bankTypeEunmArr: [
        {
          value: '0',
          label: ''
        },
        {
          value: '1',
          label: ''
        },
        {
          value: '2',
          label: ''
        }
      ],
      initAreaSelectorData: [
        {
          value: 'china',
          label: '',
          children: [],
          type: 'country',
          loading: false
        }
      ],
      tenantInfoFromValidate: {
        email: [
          {
            required: true,
            type: 'email',
            message: ',32',
            trigger: 'blur'
          }
        ],
        tenantName: [
          {
            required: true,
            message: ',10',
            max: 10,
            trigger: 'blur'
          }
        ],
        principal: [
          {
            required: true,
            message: ',20',
            max: 20,
            trigger: 'blur'
          }
        ],
        mobile: [
          {
            required: true,
            message: '',
            min: 11,
            max: 11,
            trigger: 'blur'
          }
        ],
        idCardNumber: [
          {
            required: true,
            message: '',
            min: 18,
            max: 18,
            trigger: 'blur'
          }
        ],
        corporateBankAccount: [
          {
            required: true,
            message: ',20',
            max: 20,
            trigger: 'blur'
          }
        ],
        corporateAccountName: [
          {
            required: true,
            message: ',20',
            max: 20,
            trigger: 'blur'
          }
        ],
        bankType: [
          {
            required: true,
            message: '',
            trigger: 'change'
          }
        ],
        agreeGuaranteeCash: [
          {
            required: true,
            message: '',
            trigger: 'change'
          }
        ],
        guaranteeCash: [
          {
            required: true,
            pattern: /^[0-9]+([.]{1}[0-9]{1,2})?$/,
            message: ',',
            trigger: 'blur'
          }
        ],
        provinceCode: [
          {
            required: true,
            message: '',
            trigger: 'change'
          }
        ],
        cityCode: [
          {
            required: true,
            message: '',
            trigger: 'change'
          }
        ],
        isSubsidiaryCompany: [
          {
            required: true,
            message: '',
            trigger: 'change'
          }
        ],
        status: [
          {
            required: true,
            message: '',
            trigger: 'change'
          }
        ]
      }
    }
  },
  computed: {
    breadcrumbList: function() {
      return this.utils.routerUtil.initRouterTreeNameArr(this.routerPath)
    },
    globalScreenLoding: function() {
      return this.$store.state.globalScreenLoding
    },
    globalButtonLoding: function() {
      return this.$store.state.globalButtonLoding
    }
  },
  methods: {
    loadAreaData(item, callback) {
      let apiPath = ''
      item.loading = true
      if (item.type === 'country') {
        apiPath = this.API_PTAH.provinceInfoFindAllForCascader
      }

      if (item.type === 'province') {
        apiPath = this.API_PTAH.cityInfoFindAllForCascader
      }

      this.utils.netUtil.post(
        this.$store,
        apiPath,
        { provinceCode: item.value },
        response => {
          item.children = response.data.datas
          item.loading = false
          callback()
        }
      )
    },
    handleSubmit(name) {
      this.$refs[name].validate(valid => {
        if (valid) {
          this.commitData()
        } else {
          this.$Message.error('!')
        }
      })
    },
    handleReset(name) {
      this.$refs[name].resetFields()
    },
    commitData() {
      this.$store.commit('statusGlobalButtonLoding')
      this.utils.netUtil.post(
        this.$store,
        this.API_PTAH.tenantInfoAdd,
        this.tenantInfoFrom,
        response => {
          response.data
          this.$Message.success('!')
          this.$store.commit('statusGlobalButtonLoding')
          this.$router.push('/tenant-info-list')
        },
        () => {
          this.$store.commit('statusGlobalButtonLoding')
        }
      )
    },
    AreaDataHandleChange(value, selectedData) {
      let city = selectedData.pop()
      let province = selectedData.pop()
      if (typeof city != 'undefined' && city.type === 'city') {
        this.tenantInfoFrom.cityCode = city.value
      }
      if (typeof province != 'undefined' && province.type === 'province') {
        this.tenantInfoFrom.provinceCode = province.value
      }
    }
  }
}
</script>
