<style>
</style>


<template>
  <div>
    <Row>
      <i-col span="24"
        class="bread-crumb">
        <Breadcrumb>
          <BreadcrumbItem v-for="(item,index) in breadcrumbList"
            :to="item.path"
            :key="index">
            <Icon :type="item.icon"></Icon> {{item.name}}
          </BreadcrumbItem>
        </Breadcrumb>
      </i-col>

    </Row>
    <Divider />
    <Row>
      <i-col span="12"
        offset="6">
        <Form ref="provinceInfoFrom"
          :model="provinceInfoFrom"
          :rules="provinceInfoFromValidate"
          :label-width="80">
          <FormItem label=""
            prop="provinceName">
            <i-input v-model="provinceInfoFrom.provinceName"
              placeholder=""></i-input>
          </FormItem>

          <FormItem label=""
            prop="provinceCode">
            <i-input v-model="provinceInfoFrom.provinceCode"
              placeholder=""></i-input>
          </FormItem>

          <FormItem label=""
            prop="proSort">
            <i-input v-model="provinceInfoFrom.proSort"
              placeholder=""></i-input>
          </FormItem>

          <FormItem>
            <Button type="primary"
              :disabled="globalButtonLoding"
              @click="commitData()"></Button>
          </FormItem>
        </Form>
      </i-col>
    </Row>
  </div>

</template>


<script>
export default {
  props: {
    query: { userId: 0 }
  },
  data() {
    return {
      routerPath: this.$route.path,
      provinceInfoFrom: {
        id: this.$route.params.id,
        createTime: '',
        updateTime: '',
        operatorName: '',
        appName: '',
        appCategory: ''
      },
      provinceInfoFromValidate: {
        provinceName: [
          {
            required: true,
            message: ',10',
            max: 10,
            trigger: 'blur'
          }
        ],
        provinceCode: [
          {
            required: true,
            message: ',10',
            max: 10,
            trigger: 'blur'
          }
        ],
        proSort: [
          {
            required: true,
            message: ',10',
            max: 10,
            trigger: 'blur'
          }
        ]
      }
    }
  },
  mounted: function() {
    this.loadData()
  },
  computed: {
    breadcrumbList: function() {
      return this.utils.routerUtil.initRouterTreeNameArr(this.routerPath)
    },
    globalScreenLoding: function() {
      return this.$store.state.globalScreenLoding
    },
    globalButtonLoding: function() {
      return this.$store.state.globalButtonLoding
    }
  },
  methods: {
    handleSubmit(name) {
      this.$refs[name].validate(valid => {
        if (valid) {
          this.commitData()
        } else {
          this.$Message.error('!')
        }
      })
    },
    loadData() {
      this.utils.netUtil.post(
        this.$store,
        this.API_PTAH.provinceInfoFindById,
        this.provinceInfoFrom,
        response => {
          this.provinceInfoFrom = response.data.datas
        }
      )
    },
    commitData() {
      this.$store.commit('statusGlobalButtonLoding')
      this.provinceInfoFrom.id = this.$route.params.id
      this.utils.netUtil.post(
        this.$store,
        this.API_PTAH.provinceInfoUpdate,
        this.provinceInfoFrom,
        () => {
          this.$store.commit('statusGlobalButtonLoding')
          this.$Message.success('!')
          this.$router.push('/province-info-list')
        },() => {
          this.$store.commit('statusGlobalButtonLoding')
        }
      )
    }
  }
}
</script>
