import axios from 'axios'
import { Message } from 'view-design'

let cancel,
  promiseArr = {}
const CancelToken = axios.CancelToken

//
axios.interceptors.request.use(
  config => {
    //
    if (promiseArr[config.url]) {
      promiseArr[config.url]('cancel')
      promiseArr[config.url] = cancel
    } else {
      promiseArr[config.url] = cancel
    }
    return config
  },
  error => {
    return Promise.reject(error)
  }
)

//
axios.interceptors.response.use(
  response => {
    return response
  },
  err => {
    if (err && err.response) {
      switch (err.response.status) {
        case 400:
          err.message = ''
          break
        case 401:
          err.message = ''
          break
        case 403:
          err.message = ''
          break
        case 404:
          err.message = ','
          break
        case 405:
          err.message = ''
          break
        case 408:
          err.message = ''
          break
        case 500:
          err.message = ''
          break
        case 501:
          err.message = ''
          break
        case 502:
          err.message = ''
          break
        case 503:
          err.message = ''
          break
        case 504:
          err.message = ''
          break
        case 505:
          err.message = 'http'
          break
        default:
          err.message = `${err.response.status}`
      }
    } else {
      if(err.message != 'cancel'){
        err.message = ''
      }

    }
    if(err.message != 'cancel'){
      Message.error(err.message)
    }
    return Promise.resolve(err.response)
  }
)

axios.defaults.baseURL = process.env.VUE_APP_API_ADDRESS
//

axios.defaults.headers = {
  'Content-Type': 'application/json',
  'Accept': 'application/json'
}
axios.defaults.timeout = 10000

export default {

  post: function(store,url, param, callback, errCallBack) {
    const uagUserInfo = store.state.uagCurrentLoginUserInfo
    const hedaer = {
      'Content-Type': 'application/json',
      'Accept': 'application/json',
      'uagHeaderUserInfo':encodeURI(JSON.stringify(uagUserInfo))
    }
    axios({
      headers: hedaer,
      method: 'post',
      url,
      data: param,
      cancelToken: new CancelToken(c => {
        cancel = c
      })
    }).then(response => {
      if(typeof(response) == 'undefined'){
        console.log('response is undefined,may be the request is cancelled')
        return
      } 
      if (response.data.errCode === '0') {
        callback(response)
      } else {
        if(typeof(response.data.errCode) != 'undefined'){
          Message.error(`  ${response.data.errCode} : ${response.data.errMsg}`)
        } 
        if (typeof errCallBack != 'undefined') {
          errCallBack(response)
        }
      }
    })
  }
}
