<template>
  <div>
    <Drawer :title="provinceName+' '"
      width="512"
      :closable="false"
      @on-close="onCloseTabView"
      @on-visible-change="onloadTabView"
      v-model="showDataListState">
      <Button @click="showAddFrom = true"
        type="info"></Button>
      <Divider />
      <Table :columns="columns"
        :data="listData">
        <template slot-scope="{ row, index }"
          slot="action">
          <Button type="error"
            size="small"
            @click="deleteData(row.id ,index)"
            :disabled="globalButtonLoding"
            style="margin-left: 8px"></Button>
          <!-- <a  :disabled="globalButtonLoding"></a> -->
        </template>
      </Table>
    </Drawer>

    <Drawer title=""
      :closable="false"
      @on-close="onCloseAddView"
      v-model="showAddFrom">
      <Form :label-width="80"
        ref="addDataParam"
        :rules="validateRule"
        :model="addDataParam">

        <FormItem label=""
          prop='cityName'>
          <i-input placeholder=""
            v-model="addDataParam.cityName"></i-input>
        </FormItem>

        <FormItem label=""
          prop='citySort'>
          <i-input placeholder=""
            v-model="addDataParam.citySort"></i-input>
        </FormItem>

        <FormItem>
          <Button type="primary"
            :disabled="globalButtonLoding"
            @click="handleSubmit('addDataParam')"></Button>
        </FormItem>
      </Form>
    </Drawer>

  </div>
</template>

<script>
export default {
  props: ['showCityTab', 'provinceCode', 'provinceName'],
  data() {
    return {
      paramData: {
        data: {
          provinceCode: this.provinceCode
        },
        page: {
          currentPage: 1,
          pageSize: 10
        },
        sort: {
          orderBy: 'id',
          sort: 'desc'
        }
      },
      addDataParam: {
        provinceCode: this.provinceCode,
        level: 1,
        citySort: '',
        cityName: ''
      },
      validateRule: {
        cityName: [
          {
            required: true,
            message: ',10',
            max: 20,
            trigger: 'blur'
          }
        ],
        citySort: [
          {
            required: true,
            pattern: /^[0-9]*$/,
            max: 5,
            message: ',5',
            trigger: 'blur'
          }
        ]
      },
      showDataListState: false,
      showAddFrom: false,
      columns: [
        {
          title: '',
          key: 'cityName'
        },
        {
          title: '',
          key: 'cityCode'
        },
        { title: '', slot: 'action', width: 250, align: 'center' }
      ],
      listData: []
    }
  },
  watch: {
    showCityTab: function() {
      this.showDataListState = this.showCityTab
    }
  },
  computed: {
    breadcrumbList: function() {
      return this.utils.routerUtil.initRouterTreeNameArr(this.routerPath)
    },
    globalScreenLoding: function() {
      return this.$store.state.globalScreenLoding
    },
    globalButtonLoding: function() {
      return this.$store.state.globalButtonLoding
    }
  },
  mounted: function() {
    this.getDataList()
  },
  methods: {
    handleSubmit(name) {
      this.$refs[name].validate(valid => {
        if (valid) {
          this.commitData()
        } else {
          this.$Message.error('!')
        }
      })
    },
    getDataList() {
      this.paramData.data.provinceCode = this.provinceCode
      this.utils.netUtil.post(
        this.$store,
        this.API_PTAH.cityInfoFind,
        this.paramData,
        response => {
          this.listData = response.data.datas
        }
      )
    },
    deleteData(id, index) {
      this.$store.commit('statusGlobalButtonLoding')
      this.$Modal.confirm({
        title: '',
        content: '',
        onOk: () => {
          this.utils.netUtil.post(
            this.$store,
            this.API_PTAH.cityInfoDelete,
            { id: id },
            () => {
              this.$store.commit('statusGlobalButtonLoding')
              this.listData.splice(index, 1)
              this.$Message.success('!')
            }
          )
        },
        onCancel: () => {
          this.$store.commit('statusGlobalButtonLoding')
        }
      })
    },
    commitData() {
      this.$store.commit('statusGlobalButtonLoding')
      this.addDataParam.provinceCode = this.provinceCode
      this.utils.netUtil.post(
        this.$store,
        this.API_PTAH.cityInfoAdd,
        this.addDataParam,
        () => {
          this.$store.commit('statusGlobalButtonLoding')
          this.addDataParam.ipAddress = ''
          this.showAddFrom = false
          this.$Message.success('!')
          this.getDataList()
          this.onCloseAddView()
        },
        () => {
          this.$store.commit('statusGlobalButtonLoding')
        }
      )
    },
    onCloseAddView() {
      this.addDataParam.citySort = ''
      this.addDataParam.cityName = ''
    },
    onCloseTabView() {
      this.$emit('on-close')
    },
    onloadTabView(status) {
      if (status) {
        this.getDataList()
      }
    }
  }
}
</script>

