<style>
</style>

<template>
  <div>
    <Row>
      <i-col span="24"
        class="bread-crumb">
        <Breadcrumb>
          <BreadcrumbItem v-for="(item,index) in breadcrumbList"
            :to="item.path"
            :key="index">
            <Icon :type="item.icon"></Icon> {{item.name}}
          </BreadcrumbItem>
        </Breadcrumb>
      </i-col>
    </Row>
    <Divider />
    <Form ref="queryParamFrom"
      class="query-pram-from"
      :model="paramData.data">

      <Row>

        <i-col span="4"
          offset="1"
          class="page-col">
          <FormItem prop="tenantName">
            <i-input type="text"
              placeholder=""
              v-model="paramData.data.tenantName">
            </i-input>
          </FormItem>
        </i-col>

        <i-col span="2"
          offset="1"
          class="page-col">
          <FormItem prop="principal">
            <i-input type="text"
              placeholder=""
              v-model="paramData.data.principal">
            </i-input>
          </FormItem>
        </i-col>

        <i-col span="2"
          offset="1"
          class="page-col">
          <FormItem prop="email">
            <i-input type="text"
              placeholder=""
              v-model="paramData.data.email">
            </i-input>
          </FormItem>
        </i-col>

        <i-col span="2"
          offset="1"
          class="page-col">
          <FormItem prop="mobile">
            <i-input type="text"
              placeholder=""
              v-model="paramData.data.mobile">
            </i-input>
          </FormItem>
        </i-col>

        <i-col span="5"
          offset="19"
          class="button-group">
          <Button type="info"
            @click="pushAdd()"></Button>
          <Button type="success"
            @click="getdata()"></Button>
          <Button type="warning"
            @click="handleReset()"></Button>
          <Button type="primary"
            @click="syncData()"></Button>
        </i-col>
      </Row>
    </Form>

    <br />
    <Table border
      :columns="columns"
      :data="tableData"
      class="list-button-group">
      <!-- <template slot-scope="{ row, index }" -->
      <template slot-scope="{ row }"
        slot="action">
        <Button type="warning"
          size="small"
          style="margin-right: 5px"
          v-show="showOnNeedInit(row.status)"
          @click="tenantInfoInitAccount(row.id)"></Button>
        <Button type="primary"
          size="small"
          style="margin-right: 5px"
          @click="pushEditor(row.id)"></Button>
        <!-- <Button type="error"
          size="small"
          @click="deleteData(row.id ,index)"></Button> -->
      </template>
    </Table>
    <Row>
      <i-col span="6"
        offset="18"
        class="page-col">
        <Page :total="paramData.data.totalCount"
          :current="paramData.data.currentPage"
          :page-size="paramData.data.pageSize"
          @on-change="handlePage"
          @on-page-size-change='handlePageSize'
          show-sizer
          class="pagination" />
      </i-col>
    </Row>
    <Spin size="large"
      fix
      v-if="globalScreenLoding" />
  </div>
</template>
<script>
export default {
  data() {
    return {
      routerPath: this.$route.path,
      paramData: {
        data: {},
        page: {
          currentPage: 1,
          pageSize: 10
        },
        sort: {
          orderBy: 'id',
          sort: 'desc'
        }
      },
      columns: [
        { title: '', key: 'tenantName' },
        { title: '', key: 'email' },
        { title: '', key: 'principal' },
        { title: '', key: 'mobile' },
        { title: '', key: 'provinceCode' },
        { title: '', key: 'cityCode' },
        { title: '', key: 'status' },
        { title: '', key: 'createTime' },
        { title: '', slot: 'action', width: 150, align: 'center' }
      ],
      tableData: []
    }
  },
  computed: {
    breadcrumbList: function() {
      return this.utils.routerUtil.initRouterTreeNameArr(this.routerPath)
    },
    globalScreenLoding: function() {
      return this.$store.state.globalScreenLoding
    },
    globalButtonLoding: function() {
      return this.$store.state.globalButtonLoding
    }
  },
  mounted: function() {
    this.getdata()
  },
  methods: {
    showOnNeedInit: function(status) {
      return status == ''
    },
    handlePage(value) {
      this.paramData.page.currentPage = value
      this.getdata()
    },
    tenantInfoInitAccount(id) {
      this.$store.commit('statusGlobalScreenLoding')
      this.utils.netUtil.post(
        this.$store,
        this.API_PTAH.tenantInfoInitAccount,
        { id: id },
        () => {
          this.$store.commit('statusGlobalScreenLoding')
          this.$Message.success('!')
          setTimeout(() => {
            this.getdata()
          }, 2000)
        },
        () => {
          this.$store.commit('statusGlobalScreenLoding')
        }
      )
    },
    handlePageSize(value) {
      this.paramData.page.pageSize = value
    },
    rowClassName(row, index) {
      return this.utils.styleUtil.initTableListRowClass(index)
    },
    getdata() {
      this.utils.netUtil.post(
        this.$store,
        this.API_PTAH.tenantInfoFind,
        this.paramData,
        response => {
          this.tableData = response.data.datas
          this.paramData.data.totalCount = response.data.page.totalCount
          this.paramData.data.pageSize = response.data.page.pageSize
          this.paramData.data.currentPage = response.data.page.currentPage
        }
      )
    },
    deleteData(id, index) {
      this.utils.netUtil.post(
        this.$store,
        this.API_PTAH.tenantInfoDelete,
        { id: id },
        response => {
          response.data
          this.tableData.splice(index, 1)
          this.$Message.success('!')
        }
      )
    },
    syncData() {
      this.$store.commit('statusGlobalScreenLoding')
      this.utils.netUtil.post(
        this.$store,
        this.API_PTAH.tenantInfoSync,
        {},
        () => {
          this.$store.commit('statusGlobalScreenLoding')
          this.$Message.success('!')
        },
        () => {
          this.$store.commit('statusGlobalScreenLoding')
        }
      )
    },
    handleReset() {
      this.$refs.queryParamFrom.resetFields()
      this.getdata()
    },
    pushAdd() {
      this.$router.push(`${this.$route.path}/tenant-info-add`)
    },
    pushEditor(id) {
      this.$router.push({
        path: `${this.$route.path}/tenant-info-editor/${id}`
      })
    }
  }
}
</script>